import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/login'
import userclass from '@/pages/userclass'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/class',
      name: 'class',
      component: userclass
    }

  ]
})



