<template>
    <div class="container">
        <h3>用户登录</h3>
        <div class="head-img-con">
            <div class="logo">logo</div>
        </div>
        <div class="mask-form">
            <div class="form-box">
            <div class="input-item">
                <label>用户名：</label>
                <input type="text">
            </div>
             <div class="input-item">
                <label>密码：</label>
                <input type="password" >
            </div>
            <div class="input-forget">
                <a>忘记密码？</a>
            </div>
            <div class="input-button">
                <button @click="submitHandler">登录</button>
            </div>
        </div>
        </div>
        
    </div>
    
</template>

<script>
    export default{
        data(){
            return {
                uname:'',
                pwd:'',
            }
        },

        methods:{
            submitHandler(){
                this.$router.push('/class');
            }
        }
    }
    
</script>

<style scoped>
    .container{
        width:100%;
    }

    .container h3{
        font-weight: normal;
        line-height:80px;
        font-size:38px;
        border-bottom: 1px solid #ccc;
        box-shadow: 0 3px 15px #eee;
    }

    .container .head-img-con{
        margin-top:8px;
        width:100%;
        height:350px;
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        background:white;
    }

    .head-img-con .logo{
        margin-top:80px;
        border:1px solid black;
        border-radius: 50% 50%;
        width:100px;
        height:100px;
        line-height: 100px;
    }


    .mask-form{
        padding:30px 0;
        width:100%;
        height:835px;
        background:#eee;
    }

    .form-box{
        width:80%;
        margin:30px 10%;
        box-sizing: border-box;
        border:1px solid black;
        border-radius: 10px;
        padding:20px 30px;
        background:white;
    }

    .form-box .input-item{
        margin:10px 0;
        width:100%;
        height:100px;
        font-size: 38px;
        border-bottom: 1px solid #ccc;
    }

    .input-item label{
        display: inline-block;
        text-align: right;
        width:180px;
    }

    .input-item input{
        width:300px;
        border:none;
        line-height: 100px;
    }
    
    .form-box .input-forget{
        margin:10px 0;
        width:100%;
        height:100px;
        text-align: right;
    }

    .form-box .input-button{
        margin:10px 0;
        width:100%;
        height:100px;
    }

    .input-forget a{
        font-size:30px;
        color:red;
        text-decoration: underline;
    }

    .input-button button{
        width:90%;
        height:60px;
    }
</style>



